
*{
		margin: 0;padding: 0;
}
@font-face {font-family: 'iconfont';
    src: url('./common/fontsize/iconfont.eot'); /* IE9*/
    src: url('./common/fontsize/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('./common/fontsize/iconfont.woff') format('woff'), /* chromefirefox */
    url('./common/fontsize/iconfont.ttf') format('truetype'), /* chromefirefoxoperaSafari, Android, iOS 4.2+*/
    url('./common/fontsize/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}
a{
	text-decoration: none;
}
.router-link-active{
	color: #fbf81c!important ;
}
.router-link-active i{
	color: #fbf81c!important;
}
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
.header-top{
		width: 100%;
		min-width: 1080px;
		height: 74px;
		background-image:url(common/img/header.png) ;
		overflow: hidden;		
		position: fixed;
		z-index: 10;
}
.header-top h2{
	display: inline;
	color: #333333;
	font-size: 28px;
	margin-left: 15px;
}
.logo{
	height: 60px;
	width: 60px;
	vertical-align: bottom;
	display: inline;
	margin-bottom: 14px;
	margin-left: 40px;
}
.one-list{
	height: 80px;
	overflow: hidden;
	line-height: 80px;
}
.two-list{
	height: 80px;
	background: #2984c3;
	border-bottom: 1px solid #3791cf;
	color: white;
}
.two-list a{
	color: white;
	font-size: 20px;
}
.three-list{
	height: 80px;
	background: #1b6ba2;
	border-bottom: 1px solid #3791cf;
}
.two-list1{
	overflow: hidden;
}
.one-list-tree{
	width: 210px;
	height: 80px;
	border-bottom: 1px solid #2984c3;
	background: #3497db;
	cursor: pointer;
	font-size: 22px!important;
	color: white;
}
.two-list-tree{
	width: 210px;
	height: 80px;
	cursor: pointer;
	font-size: 20px;
}
.three-list a{
	font-size: 18px;
}
.view-menu{
	width: 210px;
	position: absolute;
	min-height: 830px;
	text-indent: 1em;
	background: #41a1e3;
	position: fixed;
	top: 74px;
}
.rota{
transform:rotate(90deg);
-ms-transform:rotate(90deg); 	/* IE 9 */
-moz-transform:rotate(90deg); 	/* Firefox */
-webkit-transform:rotate(90deg); /* Safari  Chrome */
-o-transform:rotate(90deg); 	/* Opera */
}
.rota1{
transform:rotate(0deg);
-ms-transform:rotate(0deg); 	/* IE 9 */
-moz-transform:rotate(0deg); 	/* Firefox */
-webkit-transform:rotate(0deg); /* Safari  Chrome */
-o-transform:rotate(0deg);
}
#rightBox{
margin-left: 210px;
background:#f3f7f8;
min-height: 830px;
min-width: 1024px;
overflow-y: auto;
position: relative;
top: 74px;
}
.welcome{
	height: 80px;
	width: 210px;
	display: inline-block;
	text-align: center;
	line-height: 80px;
	color: #FFFFFF;
	font-size: 26px;
	background-image: url(common/img/welcome.png);
}
.img1{
	height: 18px;
	margin-left: 30px;
}
.iconfont{
	font-size: 22px;
	color: white;
	margin-right: 5px;
}
#login{
	float: right;
	display: inline-block;
	line-height: 80px;
	margin-right:50px;
	color: #666666;
}
#login a{
	color: #45A8DC;
	text-decoration: underline;
	margin-left: 10px;
	text-align: center;
}




