/**/
.ui-search{
  width: 400px;
  height: 40px;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid #e5e5e5;
  font-size: 16px;
  background: white;
}

.ui-search input{
  width: 240px;
  height: 100%;
  line-height: 100%;
  border: none;
  outline:none;
  font-size: 16px;
}
.ui-search button{
  width: 100px;
  height: 100%;
  background: #41A1E3;
  outline:none;
  border: none;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
  float: right;
}
.icon-search{
  display: inline-block;
  width: 20px;
  height: 20px;
  background: url("../../common/img/icon_search.png") no-repeat center;
  background-size: 100% 100%;
  margin: 0 10px 0 20px;
  position: relative;
  top: 5px;
}

/**/
.ui-case-list{
  overflow: hidden;
  width: 100%;
}
.ui-case-list li{
  float: left;
  height: 300px;
  box-sizing: border-box;
  background-color: #FFFFFF;
  margin: 0 2% 30px;
  width: 29%;
  min-width: 300px;
  border-radius: 10px;
  overflow: hidden;
}
.ui-case-list li:hover{
  box-shadow: 2px 2px 20px 1px rgba(50,66,100,.2);
}
.ui-case-list .ui-top{
  width: 100%;
  height: 240px;
  background: url("../img/def_case.jpg") no-repeat center;
  background-size: 100% 100%;
  position: relative;
  overflow: hidden;
}
.ui-case-list .ui-time{
  width: 95px;
  height: 30px;
  line-height: 30px;
  background-color: rgba(0,0,0,.2);
  float: right;
  color: #ffffff;
  font-size: 14px;
  text-align: right;
  margin-top: 10px;
  -webkit-border-top-left-radius:15px;
  -webkit-border-bottom-left-radius:15px;
  padding-right: 10px;
}
.ui-case-list .ui-name{
  height: 50px;
  width: 92%;
  line-height: 50px;
  text-align: left;
  font-size: 18px;
  color: #ffffff;
  position: absolute;
  bottom: 0;
  padding: 0 8%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  background-image: linear-gradient(
    to top,
    rgba(0,0,0,.3) 0%,
    rgba(0,0,0,0.2) 30%,
    rgba(0,0,0,0.1) 70%,
    rgba(0,0,0,0) 100%);
    background: -moz-linear-gradient(bottom, #efefef, #F9F900);
    filter: progid:DXImageTransform.Microsoft.Gradient(startColorStr='#efefef',endColorStr='#adadad',gradientType='0'); 
    
}
.c-top{
	height: 45px;
	margin-right:3% ;	
}
/*UI */
