
.shade{
	position: fixed;
	z-index: 100;
	background: rgba(0,0,0,.3);
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	display: none;
}
.pop-up-box{
	 width: 500px;
	 height: 300px;
	 border-radius: 10px;
	 background: #ffffff;
	 position: absolute;
	 top: 35%;
	 left: 35%;
	 z-index: 1000;
}
.pop-is{
	height:240px ;
	width:500px ;
	text-align: center;	
	line-height: 240px;
	font-size: 28px;
	color: #333333;
	border-bottom: 1px solid #d9d9d9;
}
.pop-up-box dl dt{
	width: 49.5%;
	height: 55px;
	float: left;
	font-size: 26px;
	line-height: 55px;
	text-align: center;
	color:#aaaaaa ;
	cursor: pointer;
}
.pop-up-box dl dt:nth-of-type(1){
	border-right:1px solid #d9d9d9 ;
}
.pop-up-box dl dt:nth-of-type(2){

	color: #45a8dc;
}



